.theme-switcher__toggler {
  width: 40px;
  height: 20px;
  background-image: linear-gradient(#4f2ee8 0%, #dcdcdc 100%);
  border-radius: 10px;
  padding: 1px;
  position: relative;
  box-shadow: inset 1px 1px 2px #0000001a;
}

.theme-switcher__toggler:before {
  content: "";
  width: 18px;
  height: 18px;
  background-color: var(--color-header);
  border-radius: 50%;
  transition: all 1s linear;
  position: absolute;
  top: 0;
  left: 0;
}

